import React from "react";
import PropTypes from "prop-types";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: [],
  };
  getMovies = async () => {
    // componentDidMount data  fetch 
    //  
    const {
      data: {
        // axios    data
        data: {
          // YTS API   payload(data)
          movies, // payload  movies  
        },
      },
    } = await axios.get(
      "https://yts-proxy.nomadcoders1.now.sh/list_movies.json?sort_by=rating"
    );
    this.setState({ movies, isLoading: false });
    console.log(movies); //  
  };

  componentDidMount() {
    // 6  isLoding false   We are ready  
    // setTimeout(() => {
    //   this.setState({ isLoding: false });
    // }, 6000); "We are ready"

    this.getMovies();
  }

  render() {
    const { isLoading, movies } = this.state; //  movies 
    return (
      <section class="container">
        {isLoading ? (
          <div class="loader">
            <span class="loader_text">Loading...</span>
          </div>
        ) : (
          <div class="movies">
            {movies.map((movie) => {
              return (
                <Movie
                  key={movie.id}
                  id={movie.id}
                  year={movie.year}
                  title={movie.title}
                  summary={movie.summary}
                  poster={movie.medium_cover_image}
                  genres={movie.genres}
                />
              );
            })}
          </div>
        )}
      </section>
    );
  }
}

export default App;
